.search {
  margin: 0 70px;
  padding: 80px 0 83px;
  border-bottom: 1px solid rgba(170, 170, 170, .2);
  box-sizing: border-box;
}

.searchform {
  display: flex;
  align-items: center;
}

.searchform__container {
  padding: 6px 7px 6px 22px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-grow: 3;
  background-color: white;
  border-radius: 50px;
  border: 1.5px solid rgba(170, 170, 170, .2);
}

.searchform__item {
  width: 100%;
  height: 34px;
  background: none;
  border: 0;
  padding: 0;
  outline: none;
  font-family: 'Inter';
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #a0a0a0;
}

.searchform__submit {
  width: 58px;
  height: 34px;
  padding: 8px 13px;
  background-color: rgba(43, 224, 128, 1);
  font-family: 'Inter';
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  color: white;
  border-style: none;
  border-radius: 48px;
  cursor: pointer;
  outline: none;
}

.searchform__submit:hover {
  background-color: rgba(43, 224, 128, .8);
}

@media screen and (max-width: 888px) {
  .search {
    margin: 0 30px;
    padding: 60px 0 46px;
  }

  .searchform__container {
    width: calc(100% - 32px);
  }
}

@media screen and (max-width: 768px) {
  .searchform {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 26px;
  }
}

@media screen and (max-width: 360px) {
  .search {
    min-width: calc(320px - 28px);
    margin: 0 14px;
    padding: 60px 0 33px;
  }

  .searchform__container {
    width: calc(100% - 18px);
    padding: 6px 7px 6px 16px;
  }

  .searchform {
    row-gap: 34px;
  }

  .searchform__item {
    font-size: 11px;
    line-height: 18px;
  }
}